import React, { Component } from "react";
import Switch from "../Switch";

class Toggle extends Component {
  state = {
    status: 0,
  };

  /* constructor(){
    super();
    this.handleKeyPress = this.handleKeyPress.bind(this);
    this.buttonUpdate = this.buttonUpdate.bind(this);
    this.statusCheck = this.statusCheck.bind(this);
  } */


  styles = {
    fontSize: '30px',
    fontWeight: "bold",
  };


  render() {
    var left = 100 + 'px';
    var top = 100 + 'px';
    var padding = 100 + 'px';
    return (     
      <div style={{padding, left, top,position:'absolute'}}>
        <span style={this.styles} className={this.buttonUpdate()}>{this.statusCheck()}</span>
        <button onClick={this.handleKeyPress} style={this.styles} className="button btn-secondary btn-lg">CHANGE MODE</button>
        <Switch/>
      </div>
      
    );
    
  }

  //<img src= {this.imageIterator()}/>
  imageIterator(){
    for(var i = 1;;i++){
      if(i > 10){
        i = 1;
      }
      return '/images/'+ i +'.jpg';
    }
  }

  handleKeyPress = () => {
      console.log("Clicked!");
      if(this.state.status === 0){
        this.setState({status: 1});
      }
      else if(this.state.status === 1){
        this.setState({status: 0});
      }
  }
  

  buttonUpdate = () => {
        console.log("Clicked!");
        
        if(this.state.status === 0){
            return "badge bg-success m-2";
        }

        else if(this.state.status === 1){
            return "badge bg-danger m-2";
        }

  }

  statusCheck = () => {

      if(this.state.status === 0){
          return "Automatic-Mode";
      }

      else if(this.state.status === 1){
          return "Manual-Mode";
      }
  }



}

export default Toggle;
